interface ErrorItem extends Customizable {
  message?: string;
  key?: string;
  context?: Record<string, unknown>;
}

export class ContactsError {
  message!: string;
  status?: number;
  errors?: ErrorItem[];
  additions?: Additions;
  constructor(private readonly errorResponse?: HttpErrorResponse) {
    if (this.errorResponse) {
      const { status, error } = this.errorResponse;
      this.status = status;
      if (error) {
        this.message = error.message || '';
        this.errors = error.errors || [];
        this.additions = error.additions || {};
      }
    }
  }
}

